.card {
	overflow: hidden;
	&--rounded {
		border-radius: 10px;
	}
	&--shadow {
		box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.1);
		&-hard{
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		}
	}
	&-border {
		&--dark-green{
			border: 1px solid $darkGreen;
		}
	}
	&-hover {
		&--background-tint{

			.card--background{
				background-blend-mode: multiply;
				transition: all 0.3s;
			}
			&:not(:hover){
				.card--background{
					background-color: #FFF !important;
				}
				
			}
		}
		&--glow{
			.card--glow-element, &.card--glow-element{
				transition: all 0.3s;
			}
			&:hover{
				overflow: visible;
			}
			&--light-green{
				&:hover{

					&.card--glow-element, .card--glow-element{
						box-shadow: 0px 4px 20px #86D401;
					}	
				}
				
			}
			&--black{
				&:hover{

					&.card--glow-element, .card--glow-element{
						box-shadow: 0px 4px 20px #000;
					}	
				}
				
			}
			
		}
		&--background-recolor{
			&.card--background, .card--background, &.card--force-background-opposite, .card--force-background-opposite{
				transition: all 0.3s;
				*{
					transition: all 0.3s;
				}
			}
			&--dark-green{
				&:hover{
					&.card--background, .card--background{
						background-color: $darkGreen;
						*:not(.card--mantain-text-color){

							color: $white;
						}
					}
					&.card--force-background-opposite, .card--force-background-opposite{
						background-color: $white !important;
						&:not(.card--mantain-text-color){

							color: $darkGreen;
						}
					}
					
				}
			}
		}
		&--opacity-blend{
			.card--opacity-blend-element{
				opacity: 0;
				transition: all 0.3s;
			}
			&:hover{
				.card--opacity-blend-element{
					opacity: 1;
				}
			}
		}
	}
	
}
.z{
	&--front{
		z-index: 1;
	}
	&--mid{
		z-index: 0;
	}
	&--back{
		z-index: -1;

	}
}
.background {
	&--cover {
		position: absolute;
		top: 0;
		height: 100%;
		left: 0;
		width: 100%;
	}
	&--bottom{
		position: absolute;
		bottom: 0;
		width: 100%;
	}
	&--white{
		background-color: $white;
	}
	&--dark-green {
		background-color: $darkGreen;
	}
	&--pastel-green {
		background-color: $pastelGreen;
	}
	&--opacity-0-9 {
		opacity: 0.9;
	}
	&--opacity-0-7 {
		opacity: 0.7;
	}
	&--opacity-0-1 {
		opacity: 0.1;
	}
}

a.btn{
	display: inline-block;
}
.btn{
	border-radius: 5px;
	cursor: pointer;
	text-align: center;
	border: none;
	&--full{
		display: block !important;
	}
	&--rounded{
		&-5{
			border-radius: 5px;
		}
		&-10{
			border-radius: 10px;
		}
	}
	&--S{
		padding: 10px 20px;
	}
	&--M{
		padding: 15px 35px;
		&--narrow{
			padding: 6px 40px;
		}
	}
	&--glow{
		transition: all 0.3s;
		&-light-green{
			&:hover{
				filter: drop-shadow(0px 4px 20px $lightGreen);
			}
		}
	}
	&--dark-green{
		transition: all 0.3s;
		background-color: $darkGreen;
		&:hover{
			filter: brightness(0.8);
		}
		&:active{
			filter: brightness(0.6);
		}
		&-border{
			transition: all 0.3s;
			border: 2px solid $darkGreen;
			background: $white;
			*{
				transition: all 0.3s;
			}
			&:hover{
				background: darken($darkGreen, 5);
				color: $white;
				*{
					color: $white;
				}
			}
			&:active{
				filter: brightness(0.6);
			}
		}
	}



	&--inline{
		display: inline-block !important;
	}
	&--block{
		display: block !important;
	}
}
.button-tab{
	background: $white;
	color: $darkGreen;
	border-radius: 5px;
	text-align: center;
	line-height: 1.5;
	&:not(.button-tab--auto-padding){
		padding: 10px 15px;

	}
	transition: all 0.3s;
	&.active{
		background: $darkGreen;
		color: $white;
	}
	&:not(.active){
		cursor: pointer;
		&:hover{
			filter: brightness(0.8);
		}
		&:active{
			filter: brightness(0.6);
		}	
	}
	&--narrow {
		padding-top: 7px !important;
		padding-bottom: 5px !important;
	}
	&--border{
		border: 2px solid $darkGreen;
	}
	&--active-gradient-fill{
		&.active{
			background-origin: border-box !important;
			border-color: transparent;
			background:linear-gradient(90deg, $darkGreen -10%, $lightGreen 110%);
		}
	}
}
