.filter{
	&-datepicker{
		position: relative;
		width: 12em;
		cursor: pointer;
		&__input{
			width: 100%;

			border: 1px solid #B9B9B9;
			border-radius: 4px;
			padding: 5px 10px;
			color: #6B6B6B;
			cursor: pointer;
			padding-right: 2em;
			padding-left: 1em;
		}
		&__icon{
			position: absolute;
			top: 0;
			right: 0;
			width: 2em;
			display: flex;
			justify-content: center;
			align-items: center;
			pointer-events: none;
			height: 100%;
			z-index: 1;
		}
	}
	&-dropdown{
	}
	&-search{
		background: $white;
		position: relative;
		&:not(&--auto-width){
			max-width: 25em;

		}
		input{
			border: 1px solid $darkGreen;
			border-radius: 4px;
			padding: 7px 12px;
			background: transparent;
			width: 100%;
			&:focus{
				outline: 1px solid #0004;
			}
			padding-right: 2.2em;
		}
		&__icon {
			position: absolute;
			top: 0;
			right: 0;
			width: 2em;
			display: flex;
			justify-content: center;
			align-items: center;
			pointer-events: none;
			height: 100%;
			z-index: 1;
		}
		&:has(input:focus){
			.filter-search__suggestions{
				pointer-events:auto;
				opacity: 1;
			}
		}
		&__suggestions{
			transition: all 0.2s;
			opacity: 0;
			pointer-events: none;
			border-radius: 4px;
			border: 1px solid var(--Main-Green, $darkGreen);
			background: var(--White, #FFF);
			box-shadow: 4px 4px 15px 0px rgba(0, 0, 0, 0.25);
			position: absolute;
			top: calc(100% + 25px);
			left: 0;
			right: 0;
			z-index: 100;
			padding: 10px 0;
			padding-right: 10px;
		}
		&__suggestion-wrapper {
			
			padding: 10px;
			
			max-height: 200px;
			overflow: auto;
			width: 100%;
			height: 100%;
			&::-webkit-scrollbar {
				width: 4px;
			}

			/* Track */
			&::-webkit-scrollbar-track {
				background-color: #D9D9D9;
				border-left: 1px solid transparent;
				border-right: 1px solid transparent;
				background-clip: padding-box;
			}

			/* Handle */
			&::-webkit-scrollbar-thumb {
				border-radius: 3px;
				background-color: $darkGreen;
				
			}

			/* Handle on hover */
			&::-webkit-scrollbar-thumb:hover {
				cursor: pointer;
				/*redifine styles like background and border radius*/
			}

		}
		&__suggestion {
			border-bottom: 1px solid #D9D9D9;
			padding: 5px 10px;
			cursor: pointer;
			transition: all 0.1s;
			&:hover{
				background: $darkGreen;
				color: #fff;
			}
		}

	}
	&-tag{
		background: $white;
		color: $lightGray;
		border-radius: 5px;
		text-align: center;
		padding: 4px; // 7px 15px;
		cursor: pointer;

		// border: 1px solid $darkGreen;
		transition: all 0.3s;
		&.active{
			// background: $darkGreen;
			color: $darkGreen;
		}
		//margin: 5px;
	}
}
