.progress-bar {
	position: relative;
	&--small{
		height: 2px;

		background: $darkGreen;
	}
	&--large{

		height: 4px;
		border-radius: 10px;
		background: $darkGreen;
	}
	&__elem {
		&--simple{
			border-radius: 50%;
			background: $darkGreen;
			height: 20px;
			width: 20px;
			transform: translateX(-10px);
		}
		&--yearly{
			--percentage: calc(100% * ((var(--year) - var(--start-year)) / (var(--end-year) - var(--start-year))));
			// --estimated: calc(((var(--year) - var(--start-year)) / (var(--end-year) - var(--start-year))));
			// --opposite: abs(calc(0.5 - abs(calc(0.5 - var(--estimated)))));
			// --left: calc((max(100px, 100% * var(--estimated)) - 100% * var(--estimated)));
			// --right: min(calc(100% - 100px), 100% * var(--estimated));
			// --percentage: var(--left);
			// .year-progress-bar__elem-poppup{
			// 	left: var(--left);
			// }
		}
		left: var(--percentage);
		top: 0;
		bottom: 0;
		margin: auto;
		position: absolute;
	}
}
.year-progress-bar{
	margin-top: 120px;
	margin-bottom: 60px;
	border-right: 120px solid transparent;
	border-left: 120px solid transparent;
	margin-right: 50px;
	&:before, &:after{
		content: '';
		position: absolute;
		right: -120px;
		height: 4px;
		transform-origin: calc(100% - 2px) 50%;
		width: 20px;
		background: $darkGreen;
		z-index: -1;
	}
	&:before{
		transform: rotate(-45deg);
	}
	&:after{
		transform: rotate(45deg);
	}
	&__elem{
		width: 50px;
		height: 20px;
		&--end{
			left: calc(100% + 120px );
		}
		transform: translateX(-25px);
		&-circle{
			position: absolute;
			background: #2A88F9;
			transition: all 0.3s;
			border-radius: 50%;
			width: 20px;
			height: 20px;
			left: 0;
			right: 0;
			margin: auto;
			&:before{
				content: '';
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				border: 1px solid #2A88F9;
				border-radius: 50%;
				transition: all 0.3s;
			}
		}
		&:hover{
			.year-progress-bar__elem-circle{
				background:#6FBC31;
				&:before{
					border: 1px solid #6FBC31;
					opacity: 1;
					transform: scale(1.7);
				}
			}
			.year-progress-bar__elem-poppup{
				transform: translateY(0);
				opacity: 1;
			}
		}
		&-year{
			position: absolute;
			top: 100%;
			left: -25px;
			right: -25px;
			text-align: center;
			// margin: auto;
			// width: 100px;
			padding-top: 10px;
		}
		&-poppup{
			position: absolute;
			bottom: 100%;
			left: -100px;
			right: -100px;
			text-align: center;
			padding-bottom: 15px;
			opacity: 0;
			transform: translateY(-50px);
			transition: all 0.3s;
		}
	}
}
.accordeon {
	&__trigger {
		display: flex;
		align-items: center;
		cursor: pointer;
		transition: all 0.3s;
		&:hover{
			&:before{

				--offset-mul: 0.2;
			}
		}
		&:active{
			filter: brightness(0.8);
		}
		&:before{
			content: '';
			border-left: 4px solid $white;
			border-top: 4px solid $white;

			width: 20px;
			height: 20px;

			--rotation: 225deg;
			--offset: -5px;
			--offset-mul: 1;
			transform: translateY(calc(var(--offset) * var(--offset-mul))) rotate(var(--rotation));
			transition: all 0.3s;
			margin-right: 15px;
		}
	}
	&.active{
		.accordeon__trigger{
			&:before{
				--rotation: 45deg;
				--offset: 5px;
			}
		}
	}
	&__list {
		overflow: hidden;
		height: 0;
	}
}
